// Card     
import { motion } from "framer-motion";
import { useQuery } from "react-query";
import { PathMatch, useMatch, useNavigate } from "react-router-dom";
import { styled } from "styled-components";
import { getVideoDetail, IGetDetailMovieResult } from "../../api";
import { makeImagePath } from "../../utils";

// Card         
const Overlay = styled(motion.div)`
    position: fixed;
    top: 0;
    left: 0px;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
`;
// Card     
const Info = styled(motion.div)`
    width: 50vw;
    height: 80vh;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: ${(props) => props.theme.black.lighter};
    border-radius: 20px;
    overflow: auto;

    -ms-overflow-style: none;
    scrollbar-width: none;
    &::-webkit-scrollbar {
        display: none;
    }
`;
//      
const InfoCover = styled.div`
    width: 100%;
    height: 400px;
    background-size: cover;
    background-position: center center;
    display: grid;
    grid-template-columns: 220px auto;
`;
//         div
const InfoPosterWrapper = styled.div`
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
`;
//       
const InfoPoster = styled.img`
    margin-left: 20px;
`;
//         div
const InfoTitleWrapper = styled.div``;
//       
const InfoTitle = styled.h2`
    color: ${(props) => props.theme.white.lighter};
    font-size: 35px;
    margin-top: 50px;
    margin-left: 10px;
`;
//        
const InfoOriginalTitle = styled.h4`
    font-weight: bold;
    font-size: 24px;
    margin-top: 10px;
    margin-left: 20px;
`;
//       
const InfoTagline = styled.h6`
    color: ${(props) => props.theme.white.lighter};
    font-size: 18px;
    margin-top: 10px;
    margin-left: 20px;
    &::before {
        content: " - ";
        color: ${(props) => props.theme.white.darker};
    }
`;
//        
const InfoString = styled.h6`
    color: ${(props) => props.theme.white.lighter};
    font-size: 14px;
    margin-top: 10px;
    margin-left: 25px;
    &::before {
        content: "";
        color: ${(props) => props.theme.white.darker};
    }
`;
//    div
const InfoContainer = styled.div`
    padding: 20px;
    display: grid;
    grid-template-columns: 200px 2px auto;
`;
//          div
const InfoLogo = styled.div`
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
`;
//         div
const LogoContainer = styled.div`
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
`;
//       
const Logo = styled.img`
    background-color: ${(props) => props.theme.white.lighter};
    width: 50px;
    height: 50px;
`;
//       
const InfoOverView = styled.p`
    padding: 20px;
    color: ${(props) => props.theme.white.lighter};
`;
// Div   
const HorizonBar = styled.div`
    background-color: ${(props) => props.theme.white.lighter};
    width: 100%;
    height: 100%;
`;

/**
 * Movie List Card      
 * @param keyword Search  
 * @param videoId   ID
 * @returns   Div
 */
function MovieDetail({
    keyword,
    videoId,
}: {
    keyword?: string;
    videoId: string;
}) {
    //   
    const navigate = useNavigate();
    //         
    const onOverlayClicked = () => {
        if (keyword != undefined) {
            navigate(`/search?keyword=${keyword}`);
        } else {
            navigate(`/`);
        }
    };
    //    route  
    const detailMovieMatch: PathMatch<string> | null = useMatch(
        "/movies/:category/:Id"
    );
    //  ID    
    const { data, isLoading } = useQuery<IGetDetailMovieResult>(
        ["movie", "detail"],
        () => getVideoDetail("movie", videoId)
    );

    return (
        <>
            <Overlay
                onClick={onOverlayClicked}
                animate={{ opacity: 1 }}
                exit={{ opacity: 0 }}
            />
            <Info layoutId={detailMovieMatch?.params.category + videoId}>
                <InfoCover
                    style={{
                        backgroundImage: `linear-gradient(to top,black, transparent), url(${makeImagePath(
                            String(data?.backdrop_path),
                            ""
                        )})`,
                    }}
                >
                    <InfoPosterWrapper>
                        <InfoPoster
                            src={makeImagePath(
                                String(data?.poster_path),
                                "w200"
                            )}
                        />
                    </InfoPosterWrapper>
                    <InfoTitleWrapper>
                        <InfoTitle>{data?.title}</InfoTitle>
                        <InfoOriginalTitle>
                            {data?.original_title}
                        </InfoOriginalTitle>
                        <InfoTagline>
                            {data?.tagline || " "}
                        </InfoTagline>
                        <InfoString>
                            {" : " + data?.release_date}
                        </InfoString>
                        <InfoString>
                            {" : " +
                                data?.production_countries[0]?.name}
                        </InfoString>
                        <InfoString>
                            {" : " +
                                data?.genres?.map((props) => " " + props.name)}
                        </InfoString>
                        <InfoString>
                            {" : " + data?.vote_average?.toFixed(1)}
                        </InfoString>

                        <InfoString>
                            {" : " +
                                data?.spoken_languages[0]?.english_name}
                        </InfoString>
                        <InfoString>
                            {"  : " + data?.runtime + ""}
                        </InfoString>
                    </InfoTitleWrapper>
                </InfoCover>
                <InfoContainer>
                    <InfoLogo>
                        {data?.production_companies?.map((props) => (
                            <LogoContainer key={props.id}>
                                <Logo
                                    key={props.id}
                                    src={makeImagePath(props.logo_path, "w200")}
                                    title={props.name}
                                />
                            </LogoContainer>
                        ))}
                    </InfoLogo>
                    <HorizonBar />
                    <InfoOverView>
                        {data?.overview || "  "}
                    </InfoOverView>
                </InfoContainer>
            </Info>
        </>
    );
}

export default MovieDetail;
