// Loading  
import { styled } from "styled-components";

// API    
const Loader = styled.div`
    height: 20vh;
    display: flex;
    justify-content: center;
    align-items: center;
`;

/**
 *   
 * @returns Loading...
 */
function Loading() {
    return <Loader>Loading...</Loader>;
}

export default Loading;
